.App{
    height: 100vh;
    width: 100vw;
    background-color: #faf9fb;
    overflow-x: hidden;
}
.App::-webkit-scrollbar{
    display: none;
}
header{
    display: flex;
    align-items: center;    
    justify-content: space-around;
    padding: 5px 15px;
    height:60px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.1);
    background-color: #FFF;
}

.logo{
    min-width:60px;
    width: 60px;
}
.inputBox{
    background-color: #FFF;
    border-radius: 25px;
    box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.15);
    display: flex;
    align-items: center;
    overflow: hidden;
    padding: 8px 15px;
    width: 120px;
}
.inputBox .searchIcon{
    color: #787a84;
    font-size: 20px;
}

.inputBox input{
    width: 100%;
    height: 100%;
    outline: none;
    border: none;
    background: transparent;
    color: #787a84;
    padding-left: 8px;
    font-size: 14px;
    font-weight: 500;
}

.shoppingCart{
    position: relative;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;

}

.shoppingCart .cart{
    font-size: 30px;
    color: #373848;
}
.shoppingCart .cart_content{
    width: 20px;
    height: 20px;
    background:#f8901c;
    border-radius: 100%;
    min-width: 20px;
    min-height: 20px;
    position: absolute;
    top:-8px;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;

}

.shoppingCart .cart_content p{
    font-weight: 500;
    color: #faf9fb;
}

.profioleContainer{
    display: flex;
    align-items: center;
}

.profioleContainer .imgBox{
    width: 40px;
    height: 40px;
    min-width: 40px;
    border-radius: 100vw;
    background-color: #444;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}
.profioleContainer .imgBox .profilePic{ 

    object-fit: cover;
}

.profioleContainer .userName{
    font-size: 16px;
    margin-left: 8px;
    font-weight: 500;
    display: none;
    align-items: center;

}

.toggleMenu{
    display: flow-root;
    align-items: center;
    justify-content: center;
    color: #373848;

}
.toggleIcon{
    transform: rotate(90deg);
}
